<template>
    <div>
            <app-home-loading v-if="loading"></app-home-loading>
            <div v-else>
                    <app-first></app-first>
                    <v-container fluid style="max-width: 960px">
                        <app-search
                        ></app-search>
                        <app-category
                                :categoryId="categoryId"
                                :categories="categories"
                                :filterCategory="filterCate"
                        >
                        </app-category>
                        <app-portfolio
                                :portfolios="portfolios"
                                :categories="categories"
                                :pagination="pagination"
                                :categoryId="categoryId"
                                :fetchPortfolio="fetchPort"
                                :filterCategory="filterCate"
                                :catePaginate="catePaginate"
                                :showFilter="showFilter">
                        </app-portfolio>
                    </v-container>
                <v-overlay :value="overlay">
                    <v-progress-circular color="primary" indeterminate size="100"></v-progress-circular>
                </v-overlay>
            </div>
    </div>
</template>

<script>
    import appFirst from '../../components/home/First'
    import appSearch from '../../components/home/Search'
    import appCategory from '../../components/home/Category'
    import appPortfolio from '../../components/home/Portfolio'
    import appHomeLoading from '../../components/loading/HomeLoading'
    import { mapActions, mapMutations ,mapGetters } from 'vuex'
    export default {
        components: {
            appFirst,
            appSearch,
            appCategory,
            appPortfolio,
            appHomeLoading
        },
        data: () => ({
            showFilter: true,
            categories: [],
            loading: true,
            overlay: false,
            animateOut: false
        }),
        computed: {
            ...mapGetters([
                'portfolios',
                'pagination',
                'categoryId',
                'catePaginate'
            ])
        },
        methods: {
            ...mapActions([
                'fetchPortfolios',
                'filterCategory'
            ]),
            ...mapMutations([
               'PushPORTFOLIO'
            ]),
            async fetchPort () {
                await this.fetchPortfolios(this.pagination.page)
            },
            async filterCate (page) {
                this.overlay = true
                if(this.categoryId === 'all') {
                    await this.fetchPortfolios(page)
                }
                else {
                    await this.filterCategory(page)
                }
                this.overlay = false
            },
            async fetchCategories () {
                try {
                    const results = await this.$axios.get('category/getall')
                    const categories = results.data.categories
                    categories.unshift({ name: 'ທັງໝົດ', _id: 'all' })
                    this.categories = categories
                } catch (e) {
                    throw e
                }
            }
        },
       async created () {
            const queryCate = this.$route.query.by_category
            if(!queryCate){
               await this.fetchPortfolios(1)
            }
            if(queryCate) {
                this.$store.commit('SetCATEGORY', queryCate)
               await this.filterCategory(1)
            }
            await this.fetchCategories()
            setTimeout(() => {
                this.loading = false
            }, 200)
        },
        mounted () {
            this.$io.on('postPortfolio', () =>  this.fetchPortfolios(1))
            this.$io.on('updatePortfolio', () =>  this.fetchPortfolios(1))
            this.$io.on('deletePortfolio', () =>  this.fetchPortfolios(1))
        }
    }
</script>
