<template>
    <div>
        <h2 class="text-center">
            ປະເພດ
        </h2>
        <v-card outlined>
            <swiper :options="swiperOption">
                <swiper-slide v-for="(category, index) in categories.slice(1)" :key="index" class="text-center">
                        <div class="__category" @click="filterCate(category._id)">
                            <v-img
                                    :src="$server + category.image"
                                    :lazy-src="$server + category.image"
                                    class="mx-auto my-2"
                                    max-height="50"
                                    max-width="50"
                            >
                                <template v-slot:placeholder>
                                    <v-row
                                            class="fill-height"
                                            align="center"
                                            justify="center"
                                    >
                                        <v-progress-circular size="40" indeterminate
                                                             color="primary"
                                        ></v-progress-circular>
                                    </v-row>
                                </template>
                            </v-img>
                        </div>
                    <small :class=" category._id === categoryId ? 'current-category' : ''">{{
                        category.name
                        }}</small>
                </swiper-slide>
                <div id="__swiper-pagination" class="swiper-pagination" slot="pagination"></div>
<!--                <div  class="swiper-button-prev" slot="button-prev"></div>-->
<!--                <div class="swiper-button-next" slot="button-next"></div>-->
            </swiper>
        </v-card>
    </div>

</template>

<script>
    export default {
        props: [
            'categoryId',
            'filterCategory',
            'categories'
        ],
        data:() => ({
            swiperOption: {
                slidesPerView: 7,
                spaceBetween: 20,
                breakpoints: {
                    576: {
                        slidesPerView: 3,
                        spaceBetween: 20
                    },
                    768: {
                        slidesPerView: 4,
                        spaceBetween: 20
                    },
                    992: {
                        slidesPerView: 6,
                        spaceBetween: 20
                    },
                    1200: {
                        slidesPerView: 7,
                        spaceBetween: 20
                    }
                },
                freeMode: true,
                // autoplay: {
                //   delay: 4000,
                //   disableOnInteraction: false
                // },
                pagination: {
                    el: ".swiper-pagination",
                    clickable: true,
                    dynamicBullets: true,
                    type: 'progressbar'
                },
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev'
                }
            }
        }),
        methods: {
            filterCate (id) {
                this.$store.commit('SetCATEGORY', id)
                this.filterCategory(1)
            }
        }
    }
</script>

